h3 {
  font-weight: bold;
}
.indiaStatus {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  min-width: 250px;
  margin-bottom: 50px;
  flex-wrap: wrap;
}

.selectedDataHeading {
    margin-bottom: 0;
}

.formControl {
  margin: 3% !important;
  min-width: 300px !important;
}

.selectedData {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  color: crimson;
  font-weight: bold;
}

.tiles {
  width: 100px;
  height: 150px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  font-size: medium;
  border-radius: 5px;
  margin: 2%;
  flex-wrap: wrap;
  font-weight: bold;
}
.type {
  font-weight: bold;
}
.active {
  background: rgba(250, 0, 0, 0.1);
  color: crimson;
}
.confirmed {
  background: rgba(0, 0, 250, 0.1);
  color: #5555dc;
}
.recovered {
  background: rgba(0, 250, 0, 0.1);
  color: #219021;
}
.deaths {
  background: rgba(134, 134, 134, 0.1);
  color: #424242;
}
